body{font-family:Montserrat,sans-serif}
.menu{padding:25px 40px;color:#6c6c6c;position:fixed;top:0;z-index:99;width:100%;-webkit-transition:all .3s ease;transition:all .3s ease}@media screen and (max-width:875px){.menu{display:none}}.menu h1{margin:0;position:absolute;top:50%;margin-right:-50%;-webkit-transform:translate(0,-50%);transform:translate(0,-50%)}.menu img{margin:0;position:absolute;top:50%;margin-right:-50%;-webkit-transform:translate(0,-50%);transform:translate(0,-50%);height:50px;opacity:.4;-webkit-transition:all .3s ease;transition:all .3s ease;-webkit-backface-visibility:hidden;backface-visibility:hidden}.menu .hideNavLogo{opacity:0}.menuBackground{background-color:#181818;padding:12px 40px}.bm-burger-button{position:fixed;width:40px;height:30px;left:20px;top:15px}@media screen and (min-width:875px){.bm-burger-button{display:none}}.bm-burger-bars{background:#fff}.bm-cross-button{height:24px;width:24px}.bm-cross{background:#fff}.bm-menu{background:#181818;padding-top:100px;font-size:40px}.bm-menu-wrap,.bm-overlay{top:0;left:0}@media screen and (min-width:875px){.bm-menu-wrap,.bm-overlay{display:none}}.bm-header{background:#131313;position:absolute;top:0;height:100px;width:100%}.bm-header img{width:65px;margin:0;position:absolute;top:50%;left:50%;margin-right:-50%;-webkit-transform:translate(-50%,-50%);transform:translate(-50%,-50%)}.bm-burger-menu{background:#181818;height:60px;width:100%;position:fixed;z-index:1;-webkit-transition:all .3s ease;transition:all .3s ease}.bm-burger-menu img{margin:0;position:absolute;width:50px;height:50px;top:50%;left:50%;margin-right:-50%;-webkit-transform:translate(-50%,-50%);transform:translate(-50%,-50%);opacity:.4;-webkit-transition:all .3s ease;transition:all .3s ease;-webkit-backface-visibility:hidden;backface-visibility:hidden}@media screen and (min-width:875px){.bm-burger-menu{display:none}}.camera a:after,.camera a:before{height:14px;width:14px;position:absolute;content:"";-webkit-transition:all .35s ease;transition:all .35s ease;opacity:0}.camera{text-align:center;text-transform:uppercase;letter-spacing:1px;margin:0;padding:0}.camera *{box-sizing:border-box;-webkit-transition:all .35s ease;transition:all .35s ease}.camera li{display:inline-block;list-style:outside none none;margin:0 1em;padding:0}.camera a{padding:.5em .8em;margin:.2em 0;display:block;color:rgba(255,255,255,.5);position:relative;text-decoration:none;font-size:12px}.camera a:before{left:0;top:0;border-left:3px solid #e61212;border-top:3px solid #e61212;-webkit-transform:translate(100%,50%);transform:translate(100%,50%)}.camera a:after{right:0;bottom:0;border-right:3px solid #e61212;border-bottom:3px solid #e61212;-webkit-transform:translate(-100%,-50%);transform:translate(-100%,-50%)}.camera a:hover{color:#fff}.camera a:hover:before{-webkit-transform:translate(0,0);transform:translate(0,0);opacity:1}.camera a:hover:after{-webkit-transform:translate(0,0);transform:translate(0,0);opacity:1}@media screen and (min-width:1200px){.camera a{font-size:16px}}.camera .current a{color:#fff}.camera .current a:before{-webkit-transform:translate(0,0);transform:translate(0,0);opacity:1}.camera .current a:after{-webkit-transform:translate(0,0);transform:translate(0,0);opacity:1}
.parallax .parallax-image-container{height:350px;background:#000000ab}@media screen and (max-width:600px){.parallax .parallax-image-container{height:265px}}@media screen and (min-width:1200px){.parallax .parallax-image-container{height:450px}}.parallax .parallaxTextContainer{margin:0;margin-right:-50%;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%,-50%);transform:translate(-50%,-50%);text-align:center}.parallax .parallaxTextContainer h1{font-size:60px;font-weight:600;color:#fff;letter-spacing:5px;text-transform:uppercase}@media screen and (max-width:600px){.parallax .parallaxTextContainer h1{font-size:35px}}@media screen and (min-width:1200px){.parallax .parallaxTextContainer h1{font-size:80px}}.parallax .parallaxTextContainer h1 span{color:#e61212;font-size:180%}.parallax .parallaxTextContainer h2{margin:0;font-size:13px;color:#fff;letter-spacing:5px;text-transform:uppercase;line-height:25px;padding:0 10px 0 10px}@media screen and (max-width:600px){.parallax .parallaxTextContainer h2{font-size:11px}}@media screen and (min-width:1200px){.parallax .parallaxTextContainer h2{font-size:16px}}.homeHeaderContent{position:absolute;text-align:center;width:100%;top:60%;left:50%;margin-right:-50%;-webkit-transform:translate(-50%,-50%);transform:translate(-50%,-50%)}.homeHeaderContent .buttonWrapper button{margin:5px 4px 30px 4px;padding:8px;font-size:14px;width:210px}@media screen and (max-width:600px){.homeHeaderContent .buttonWrapper button{margin:2px 2px 5px 2px;padding:4px;font-size:12px;width:150px}}@media screen and (min-width:1200px){.homeHeaderContent .buttonWrapper button{margin:10px 6px 35px 6px;padding:12px;font-size:16px;width:270px}}.logo{width:210px;height:210px}@media screen and (max-width:600px){.logo{width:150px;height:150px}}@media screen and (min-width:1200px){.logo{width:270px;height:270px}}
